.slider {
    display: flex;
    align-items: center;
    position: relative;
    margin-bottom: 30px;
    mix-blend-mode: normal;

    &_picture {
        width: calc(100% - 148px);
        height: 415px;
        -o-object-fit: cover;
        object-fit: cover;
        border-radius: 25px;
        margin: auto;
        display: flex;
        justify-content: center;

        @media (min-width: 767.99px) and (max-width :1024px) {
            width: calc(100% - 85px);
            justify-content: space-around;
        }

        @media (min-width: 374.99px) and (max-width :768px) {
            justify-content: space-around;
        }
    }

    &_number {
        position: absolute;
        bottom: 30px;
        left: 50%;
        color: white;
        font-size: 18px;
        font-weight: 500;
        text-shadow: black 1px 1px;
    }

    &_left {
        width: 46px;
        height: 79px;
        cursor: pointer;
        position: fixed;
        left: 6%;
        color: white;

        @media (min-width: 374.99px) and (max-width :768px) {
            width: 23px;
            height: 23px;
            left: 14%;

        }
    }

    &_right {
        width: 46px;
        height: 79px;
        cursor: pointer;
        position: fixed;
        right: 17%;

        @media (min-width: 374.99px) and (max-width :768px) {
            width: 23px;
            height: 23px;
            right: 26%;

        }
    }

    @keyframes monSlide {
        0% {
            left: 0px;
        }

        100% {
            left: -1800px;
        }
    }
}