.card {
    position: relative;
    width: 340px;
    height: 340px;
    border-radius: 10px;
    flex: auto;
  
    @media (min-width: 767.99px) and (max-width :1024px) {
        max-width: 260px;
        max-height: 260px;
    } 

    @media (min-width: 424.99px) and (max-width: 767.99px) {
       width: 100%;
    }


&_picture{
    width: 100%;
    height: 100%;
    border-radius: 10px;
    object-fit: cover;

   
}

&_title{
    position: absolute;
    margin: 0;
    left: 20px;
    right: 20px;
    bottom: 20px;
    font-size: 18px;
    font-weight: 500;
    line-height: 142.6%;
    text-align: left;
    color: white;
}
}

